.headerpage {
    margin-left: 2rem;
    margin-right: 2rem;
    z-index: 1;
}

.bg-amenities {
    background-image: linear-gradient( rgb(62, 111, 203), #544f3f);
}

.container-items {
    justify-content: space-evenly;
    align-content: space-evenly;
    align-items: center;
}

.container {
    justify-content: space-evenly;
    justify-items: center;
    align-content: space-evenly;
    align-items: center;
}

.before-open {
    transform: translateY(-5%);
    transition: transform 0.5s ease-in-out  
}

.open {
    transform: translateY(0%);
    transition: transform 0.5s ease-in-out  
}

.bgcontact {
    background-image: linear-gradient( rgb(74, 75, 77), #544f3f);
}

.image-container {
    overflow: hidden;
}
  
.hover-animate:hover {
    transform: scale(1.1);transition: transform 0.3s ease-out;
}